import {React, Component} from "react"
let config = window.config;

class AdsenseComponent extends Component {
	constructor(props) {
		super(props);
		this.state = {
			shouldUpdate: null,
		};
		this.settings = {
			timeout: 700,
		};
		this.adElementStyle = {
			width: 80 + "%",
			height: 14 + "rem",
			display: "block",
			marginLeft: "auto",
			marginRight: "auto",
		};
	}

	componentDidMount() {
		if (config.adsense.adsenseId !== "" && config.adsense.enabled === true) {
			if (config.adsense.adSlot !== "") {
				let el = document.createElement("script");
				el.defer="defer";
				el.src = `https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js`;
				el.crossorigin = "anonymous";
				document.body.appendChild(el);
				window.adsbygoogle.push({});
			}
		}
	}

	render() {
		return config.adsense.adsenseId !== "" &&
			config.adsense.enabled === true ? (
			<div className="row py-4 text-center">
				<ins
					className="adsbygoogle"
					style={this.adElementStyle}
					data-ad-client={`ca-pub-${config.adsense.adsenseId}`}
					data-ad-slot={config.adsense.adSlot}
					data-ad-layout="in-article"
					data-ad-format="fluid"
					data-adtest="on"
				></ins>
			</div>
		) : (
			""
		);
	}
}

export default AdsenseComponent