import { React, Component } from "react";

class TikTok extends Component {
	constructor(props) {
		super(props);
		this.tiktokini = this.tiktokmount.bind(this);
		this.tiktokupdate = this.tiktokupdate.bind(this);
		this.scriptInjector = this.scriptInjector.bind(this);
		this.removal = this.removal.bind(this);
		this.settings = {
			scriptName: "Tiktokscript",
		};
	}

	scriptInjector() {
		let tScript = document.createElement("script");
		tScript.id = this.settings.scriptName;
		tScript.defer = "defer";
		tScript.src = "https://www.tiktok.com/embed.js";
		document.body.appendChild(tScript);
	}

	removal() {
		let removingEl = document.querySelector(`#${this.settings.scriptName}`);
		try {
			document.body.removeChild(removingEl);

			this.scriptInjector();
		} catch (error) {
			this.scriptInjector();
		}
	}

	tiktokmount() {
		if (document.querySelectorAll(".tiktok-embed").length) {
			this.removal();
		}
	}

	tiktokupdate() {
		if (document.querySelectorAll(".tiktok-embed").length) {
			this.removal();
		}
	}

	componentDidMount() {
		this.tiktokmount();
	}

	componentDidUpdate() {
		this.tiktokupdate();
	}

	render() {
		return <div id="TikTokSupport" />;
	}
}

export default TikTok;
