import { React, Component } from "react";
let config = window.config;

class CardImageelement extends Component {
	constructor(props) {
		super(props);
		this.cardStyle = {
			maxHeight: 26 + "rem",
			minHeight: 26 + "rem",
		};
		this.state = {
			imageSource: null,
			imageElement: null,
		};
	}

	componentDidMount() {
		this.setState({
			imageSource: this.props.sourceinfo.images,
			imageElement:
				typeof this.props.sourceinfo.images !== "undefined"
					? this.props.sourceinfo.images[0]
						? this.props.sourceinfo.images[0].url
						: config.graphImageUrl
					: config.graphImageUrl,
		});
	}

	render() {
		return (
			<img
				style={this.cardStyle}
				decoding="async"
				loading="lazy"
				className="card-img-top"
				src={
					this.state.imageSource !== null &&
					this.state.imageElement !== config.graphImageUrl
						? this.state.imageElement
						: config.graphImageUrl
				}
				alt={window.location.hostname}
			/>
		);
	}
}

export default CardImageelement;
